import React from 'react'
import {NavLink, Route, Redirect,
    useHistory,
    useLocation,
    useParams,
    useRouteMatch,
    withRouter,
    Switch 
} from 'react-router-dom'

function PrivateRoute({ component:Com, routes:abc, ...rest }) {
	return (
		<Route
			{ ...rest }
			render={ ({ location }) =>
				localStorage.getItem('userinfo')
                    ? <Com { ...rest } routes={ abc }/>
                    : (
                        <Redirect
                            to={ {
                                pathname: "/login",
                                state: location
                            } }
                        />
                    )
			}
		/>
	);
}

export default PrivateRoute